import React from 'react';
import styles from './navbar.module.css'

class Navbar extends React.Component {
    state = {selectedIndex: 0}

    cloneChild = (child, index) => {
        if(child.type === NavButton) {
            return React.cloneElement(child, {
                selected: this.state.selectedIndex === index,
                onClick: () => this.select(index)
            })
        } else {
            return child
        }
    }

    select = index => {
        this.setState({
            selectedIndex: index
        })
    }

    render() {
        const children = React.Children.map(this.props.children, this.cloneChild)
        return (
            <>
            <div className={styles.Navbar}>{children}</div>
            {children[this.state.selectedIndex].props.linkTo}
            </>
        );
    }
}

class NavButton extends React.Component {
    render() {
        return (
            <span className={`${styles.NavButton} ${this.props.selected ? styles.selected : ''}`} onClick={this.props.onClick}>
                {this.props.children}
            </span>
        )
    }
}

export {NavButton};
export default Navbar;